#certificates-section {
    min-height: 90vh;
    height: fit-content;
    width: 89.5vw;
    color: var(--font-primary-color);
    padding: 5vw;
    color: var(--font-highlight-color);
}

#v1-button {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: var(--font-highlight-color);
}

#v1-button-content {
    padding: 1vw;
    border: 2px solid var(--primary-color);
    border-radius: 40px;
    background-color: var(--secondary-color);
}

#v1-button a{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
    cursor: pointer;
    color: var(--font-highlight-color);
    background-color: var(--bg-color);
    border-radius: 40px;
    padding: 10px 0;
    font-weight: bolder;
}

#v1-button #legacy-tech {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

#legacy-tech img {
    margin: 0.5rem;
    padding: 0.5rem;
    height: 20px;
    width: auto;
    background-color: var(--bg-color);
    border-radius: 50%;
}

#search-bar {
    display: flex;
    margin: 20px;
    padding: 0 10px;
    justify-content: space-evenly;
    align-items: center;
    border: 2px solid var(--font-secondary-color);
    border-radius: 50px;
}

#search-input {
    width: 100%;
    padding: 1%;
    margin: 5px 0;
    border: none;
    background-color: transparent;
    color: var(--font-highlight-color);
    font-size: 1rem;
}

#search-input::placeholder {
    color: var(--font-secondary-color);
}

#search-input:focus {
    outline: none;
}
  
#search-input::-ms-input-placeholder {
    color: var(--font-secondary-color);
}

#certificates{
    display:flex;
    flex-wrap: wrap;
    scroll-snap-type: x mandatory;
    scroll-behavior: smooth;
    padding: 1%;
    justify-content: space-evenly;
}

.certificate-front, .certificate-back{
    padding: 1%;
    margin: 1%;
    height: 25rem;
    width: 20rem;
    scroll-snap-align: start;
    flex-shrink: 0;
    border: 2px solid var(--primary-color);
    border-radius: 20px;
    background: var(--secondary-color);
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

@media (max-width: 800px) {
    #certificates {
        flex-wrap: nowrap;
        justify-content: space-between;
        overflow-x: scroll;
    }

    .certificate-front, .certificate-back{
        padding: 0;
        margin: 1% 10px;
    }

    #v1-button {
        padding-top: 3%;
    }
}

.certificate-title {
    font-size: larger;
    font-weight: bolder;
    text-align: center;
    color: var(--bg-color);
}

.certificate-icons {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    padding-right: 1%;
}

.certificate-categories, .certificate-issuing-auth, .certificate-topics {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    min-width: 100%;
}

.certificate-category {
    padding: 0 5px;
    border: 1px solid var(--bg-color);
    border-radius: 20px;
    box-shadow: 2px 2px 3px black;
}

.certificate-topics {
    flex-direction: column;
}

.certificate-topic {
    padding: 0 20px;
    color: rgba(0, 0, 0, 0.678);
    font-weight:500;
}

.certificate-auth {
    padding: 0 5px;
    border: 1px solid var(--bg-color);
    border-radius: 5px;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.336);
}

.certificate-id {
    width: 100%;
    text-wrap: pretty;
}

.verify-button, .flip-button, .open-button{
    border: 2px solid var(--primary-color);
    border-radius: 20px;
    background-color: var(--secondary-color);
    width: 40%;
    color: var(--font-highlight-color);
    text-align: center;
    cursor: pointer;
}

.verify-button a, .open-button a{
    text-decoration: none;
    color: var(--font-highlight-color);
}

.flip-button {
    font-weight: bolder;
    color: var(--bg-color);
}

.certificate-thumbnail {
    width: 280px;
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 172, 240, 0.1);
    border: 2px dashed var(--font-secondary-color);
    border-radius: 10px;
    color: var(--font-secondary-color);
}

.thumbnail-icon {
    font-size: 4rem;
    margin-bottom: 10px;
}

.thumbnail-text {
    font-size: 0.9rem;
    text-align: center;
    padding: 0 10px;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.thumbnail-loading {
    font-size: 0.8rem;
    margin-top: 10px;
    color: var(--font-primary-color);
    animation: pulse 1.5s ease-in-out infinite;
}

@keyframes pulse {
    0%, 100% {
        opacity: 1;
    }
    50% {
        opacity: 0.5;
    }
}